form {
    display: inline-block;
    margin-left: 40px;
    padding-top: 60px;
}

input[type="text"] {
    border-bottom: solid 1px var(--red-accent) !important;
    font-size: 17px;
    margin: 0 20px;
    width: 160px;
    line-height: 2em;
}

button {
    background-color: var(--red-accent) !important;
    padding: 10px 15px;
    border-radius: 4px;
    margin-left: 20px;
    color: var(--white-base);
}

button:hover {
    background-color: #ff243d !important;
}




