.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 1100px;
    height: 90vh;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: 40px;
    position: absolute;
    border-radius: 20px;
    -webkit-box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
            box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);

    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
}

.logo-container {
    -ms-flex: 1 1;
        flex: 1 1;
    /*background-color: var(--grey-base);*/
    background: url("https://5dwallpaper.com/wp-content/uploads/2017/07/Stars-And-Moon-Winter-Mountain-Landscape-iPhone-HD-http-freebestpicture-com-stars-a-wallpaper-wp0011689.jpg");
    background-size: cover;
    margin-right: -1px;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
}

.info-container {
    -ms-flex: 1.5 1;
        flex: 1.5 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background: -webkit-gradient(linear, left top, left bottom, from(var(--grey-base)), to(#404363));
    background: -webkit-linear-gradient(top, var(--grey-base), #404363);
    background: -o-linear-gradient(top, var(--grey-base), #404363);
    background: linear-gradient(to bottom, var(--grey-base), #404363);
    border-top-right-radius: 6px;
    border-bottom-right-radius: 6px;
}

.info-container .form-container {
    -ms-flex: 1 1;
        flex: 1 1;
}

.info-container .weather-container {
    -ms-flex: 4 1;
        flex: 4 1;
}

.footer {
    text-align: center;
    font-size: 13px;
    padding-bottom: 5px;
}




